<template>
  <div class="hello">
    <div class="list-view" @scroll="handleScroll">
      <div
        class="list-view-phantom"       
        :style="{
           height: contentHeight
        }">
      </div>
      <div ref="content" class="list-view-content">
        <div class="list-view-item" :style="{height: itemHeight + 'px'}"
          v-for="item in visibleData" :key="item.value"> 
          {{ item }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      visibleData: [],
      itemHeight:30,
    };
  },
  computed: {
    contentHeight() {
        return this.visibleData.length * this.itemHeight + 'px';
    }
  },
  methods: {
    updateVisibleData(scrollTop) {
      console.log(scrollTop)
      scrollTop = scrollTop || 0;

      const visibleCount = Math.ceil(this.$el.clientHeight / this.itemHeight);
      console.log("visibleCount: ",visibleCount)
      const start = Math.floor(scrollTop / this.itemHeight);
      console.log(start)
      // const end = start + visibleCount;
      // this.visibleData = this.data.slice(start, end);
      // this.$refs.content.style.webkitTransform = `translate3d(0, ${ start * this.itemHeight }px, 0)`;
    },

    handleScroll(e) {
      console.log(e)
      const scrollTop = e.target.scrollTop;
      
      console.log(e.target.scrollTop)
      this.updateVisibleData(scrollTop);
    }
  },
  mounted() {
    for(let i=0;i<100;i++){
      this.visibleData.push(i)
    }
    this.updateVisibleData();
  },
  
}
</script>

